.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  margin-bottom: 16px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
  margin-top: 10px;
}

.mark-red {
    color:#ff0000;
    background: none;
}
.mark-yellow {
  color:#ffee00;
  background: none;
}

